"use client";

import { useState } from "react";
import { Plus, X, Tag } from "lucide-react";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";

// Mock initial tags
const initialTags = [
    { id: 1, name: "Mistake: FOMO", category: "Mistake", color: "bg-red-500/20 text-red-500" },
    { id: 2, name: "Strategy: Breakout", category: "Strategy", color: "bg-blue-500/20 text-blue-500" },
    { id: 3, name: "Time: Morning", category: "Time Frame", color: "bg-yellow-500/20 text-yellow-500" },
    { id: 4, name: "Emotion: Angry", category: "Emotion", color: "bg-orange-500/20 text-orange-500" },
    { id: 5, name: "Setup: A+", category: "Quality", color: "bg-green-500/20 text-green-500" },
];

export default function TagsPage() {
    const [tags, setTags] = useState(initialTags);
    const [newTag, setNewTag] = useState("");

    const handleAddTag = () => {
        if (!newTag.trim()) return;
        const tag = {
            id: Date.now(),
            name: newTag,
            category: "Custom",
            color: "bg-secondary text-secondary-foreground"
        };
        setTags([...tags, tag]);
        setNewTag("");
    };

    const handleDeleteTag = (id: number) => {
        setTags(tags.filter(t => t.id !== id));
    };

    return (
        <div className="flex-1 space-y-4 p-8 pt-6">
            <div className="flex items-center justify-between space-y-2">
                <h2 className="text-3xl font-bold tracking-tight">Tag Management</h2>
            </div>

            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-7">
                <Card className="col-span-4">
                    <CardHeader>
                        <CardTitle>Existing Tags</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="flex flex-wrap gap-2">
                            {tags.map((tag) => (
                                <div
                                    key={tag.id}
                                    className={`flex items-center space-x-2 px-3 py-1.5 rounded-full text-sm font-medium border border-transparent ${tag.color}`}
                                >
                                    <Tag className="h-3 w-3" />
                                    <span>{tag.name}</span>
                                    <button onClick={() => handleDeleteTag(tag.id)} className="hover:text-foreground/70"><X className="h-3 w-3" /></button>
                                </div>
                            ))}
                        </div>
                    </CardContent>
                </Card>

                <Card className="col-span-3">
                    <CardHeader>
                        <CardTitle>Create New Tag</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="flex space-x-2">
                            <input
                                type="text"
                                value={newTag}
                                onChange={(e) => setNewTag(e.target.value)}
                                placeholder="e.g., Strategy: Reversal"
                                className="flex-1 bg-background border border-input px-3 py-2 rounded-md text-sm ring-offset-background placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring"
                                onKeyDown={(e) => e.key === 'Enter' && handleAddTag()}
                            />
                            <button
                                onClick={handleAddTag}
                                className="bg-primary text-primary-foreground hover:bg-primary/90 px-4 py-2 rounded-md text-sm font-medium"
                            >
                                <Plus className="h-4 w-4" />
                            </button>
                        </div>
                        <p className="text-xs text-muted-foreground mt-4">
                            Tip: Use prefixes like "Mistake:", "Strategy:" to group your tags effectively.
                        </p>
                    </CardContent>
                </Card>
            </div>
        </div>
    );
}
